<template>
  <div id="app">
    <h2 is="sui-header" icon text-align="center">
      <sui-icon name="cloud" circular />
      <sui-header-content>search engine</sui-header-content>
    </h2>
    <div id="container">
      <sui-input v-model.lazy="query" id="bodySearchBar" placeholder="Search body..." />
      <br>
      <div id="info" v-if="error">
        <sui-segment color="red">
          Error!
          <pre>{{error.error.msg}}</pre>
        </sui-segment>
      </div>
      <div id="resultList" v-if="result">
        <template v-if="result.response.docs.length > 0">
          <p>검색결과: {{result.response.numFound}}개</p>
          <p>현재 페이지: {{page}}</p>
        </template>
        <sui-segment color="blue" v-for="doc in result.response.docs" v-bind:key="doc.id">
          <Item v-bind:data="doc" />
        </sui-segment>
        <sui-segment color="red" v-if="result.response.docs.length == 0 && query">No result or wrong query</sui-segment>
        <paginate
          v-model="page"
          :page-count="~~(result.response.numFound / 30) + 1"
          :page-range="15"
          :click-handler="pageChanged"
          :prev-text="'<<<<'"
          :next-text="'>>>>'"
          :container-class="'ui menu page'"
          :next-class="'item pageItem'"
          :prev-class="'item pageItem'"
          :page-class="'item pageItem'"
          v-if="result.response.docs.length > 0"
          >
        </paginate>

      </div>
      <div id="wait" v-if="waiting">
        <sui-icon size="big" name="spinner" loading/>
      </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import Item from './components/Item.vue'
import * as _ from 'lodash'
import paginate from 'vuejs-paginate'


const axios = require('axios').default;

export default {
  name: 'App',
  components: {
    Item, paginate
  },
  data: ()=>{
    return {
      query: "",
      result: null,
      error: null,
      waiting: false,
      start: 0,
      page: 1,
    }
  },
  methods:{
    pageChanged: function(page){
      console.log(page)
      this.start = (page - 1) * 30
      this.sendquery()
      window.scrollTo(0,0)
    },
    sendquery: function(){
      this.waiting = true
      this.result = null
      axios.get('http://fuzzer.ismy.cat:8983/solr/tmp/select', {
        params:{
          q: this.query,
          rows: 30,
          start: this.start,
          fl: '*,body:[value v=""]'
        }
      }).then((response) => {
        this.error = null
        this.waiting = false
        this.result = response.data
      }).catch((error) => {
        this.waiting = false
        this.result = null
        this.error = error.response.data
        console.log(error.response)
      })
    }
  },
  watch:{
    query: _.debounce(function(){
      this.start = 0
      this.page = 1
      this.sendquery()
    }, 750)
  }
}
</script>

