<template>
  <div id="container">
    <div id="left">
      <h2 is="sui-header" icon text-align="center">
        <sui-header-content id="ip">
          <a :href="data.id">{{data.id}}</a>
        </sui-header-content>
      </h2>
      <sui-segment class="nopadding">
        <sui-loader v-if="isLoading" active>Loading...</sui-loader>
        <sui-image fluid id="siteImage" :src="imgurl">
          <div is="sui-label" :color="connectColor" slot="label" ribbon>
            <sui-icon name="spinner" loading v-if="connectColor == 'yellow'"/>
            <template v-else-if="connectColor == 'green'">
              UP
            </template>
            <template v-else-if="connectColor == 'red'">
              DOWN
            </template>
          </div>
        </sui-image>
      </sui-segment>
    </div>
    <sui-table id="table" striped>
      <sui-table-body>
        <sui-table-row v-for="(value, key) in data" v-if="[
          'body', 'path', 'port', 'ip', 'headers.Date', '_version_',
          'headers.Connection', 'protocol', 'version', 'headers.Content-Encoding',
          'id', 'headers.Transfer-Encoding', 'headers.ETag',
        ].indexOf(key) == -1">
          <sui-table-cell class="kk">{{key}}</sui-table-cell>
          <sui-table-cell class="vv" v-if="typeof value === 'object'">{{value[0]}}</sui-table-cell>
          <sui-table-cell class="vv" v-else>{{value}}</sui-table-cell>
        </sui-table-row>
      </sui-table-body>
    </sui-table>
  </div>
</template>

<script>
const axios = require('axios').default;

export default {
  name: 'Item',
  props: ['data'],
  data: ()=>{
    return {
      connectColor: 'yellow',
      imgurl: '',
      isLoading: true
    }
  },
  methods: {
    error: function(){
      this.isLoading = false
    }
  },
  watch: {
    connectColor: function(){
      if(this.connectColor != 'green'){
        return
      }
      this.imgurl = `http://crawl${~~(Math.random() * 8)}.ismy.cat:3000/screenshot?url=${this.data.id}`
    }
  },
  mounted: function(){
    this.$el.querySelector('#siteImage > img').onload = this.error
    Math.random() * 3
    axios.get(`http://crawl${~~(Math.random() * 8)}.ismy.cat:3000/ping`, {
      params: {
        ip: this.data.ip[0],
        port: this.data.port[0]
      }
    }).then((response) => {
      if(response.data === true){
        this.connectColor = 'green'
      }
      else{
        this.connectColor = 'red'
        this.isLoading = false
      }
    })
  }
}
</script>

